<template>
    <div class="app app-header-fixed">
        <div id="content" role="main">
            <div class="app-content-body ">
                <div class="wrapper-md control">
                    <div class="row">
                        <div class="col-xs-12 col-sm-10 col-md-8 col-lg-6 center-block" style="float: none;">
                            <div class="alert alert-info text-md">
                                <p>详细内容请访问：<a href="https://www.cprk.cc/p/ypay-1.html">Prk博客</a>！当前版本：V1.0.1</p>
                            </div>
                            <div class="panel panel-default">
                                <div class="panel-heading font-bold">
                                    <i class="fa fa-eye"></i>&nbsp;源支付计划任务<a class="btn btn-default btn-xs pull-right" title="刷新" @click="refreshPage()"><i class="fa fa-refresh"></i></a>
                                </div>
                                <div class="panel-body">
                                    <form class="form-horizontal devform">
                                        <div class="form-group">
                                            <label class="col-sm-3 control-label">网站地址</label>
                                            <div class="col-sm-8">
                                                <input class="form-control" type="text" name="url" v-model="url" placeholder="网站地址，https:// 开头，结尾不要有 / ！" />
                                            </div>
                                        </div>
                                        <div class="form-group">
                                            <label class="col-sm-3 control-label">刷新秒数</label>
                                            <div class="col-sm-8">
                                                <input class="form-control" type="text" name="ms" v-model.number="ms" placeholder="单位：毫秒，推荐5000毫秒" />
                                            </div>
                                        </div>
                                        <div class="form-group">
                                            <label class="col-sm-3 control-label">请求方式</label>
                                            <div class="col-sm-8">
                                                <div class="radio">
                                                    <label class="i-checks">
                                                        <input type="radio" name="type" v-model="methods" value="wechat" rate="100"><i></i>微信
                                                    </label>&nbsp;
                                                    <label class="i-checks">
                                                        <input type="radio" name="type" v-model="methods" value="alipay" rate="100"><i></i>支付宝
                                                    </label>&nbsp;
                                                    <label class="i-checks">
                                                        <input type="radio" name="type" v-model="methods" value="all" rate="100" checked="checked"><i></i>两者
                                                    </label>&nbsp;
                                                </div>
                                            </div>
                                        </div>
                                        <div class="form-group">
                                            <label class="col-sm-3 control-label">请求日志</label>
                                            <div class="col-sm-8">
                                                <input class="form-control" type="text" v-model="log" placeholder="有内容就会出现日志" disabled />
                                            </div>
                                        </div>
                                        <div class="form-group">
                                            <div class="col-sm-offset-3 col-sm-8">
                                                <input type="button" id="submit" value="开启" class="btn btn-success form-control" @click="setTimer()" v-if="running===false" />
                                                <input type="button" id="submit" value="运行中... 关闭" class="btn btn-danger form-control" @click="closeTimer()" v-if="running===true" />
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import '../assets/css/app.css'
    import '../assets/css/font.css'
    // import '../../node_modules/layui/dist/css/layui.css'
    // import '../../node_modules/bootstrap/dist/css/bootstrap.min.css'
    // import '../../node_modules/bootstrap/dist/js/bootstrap.min.js'
    // import '../../node_modules/font-awesome/css/font-awesome.min.css'
    // import '../../node_modules/simple-line-icons/css/simple-line-icons.css'
    // import '../../node_modules/jquery/dist/jquery.min.js'

    export default {
        name: 'Index',
        data() {
            return {
                url: '',
                ms: '',
                methods: 'all',
                running: false,
                wechatLog: '微信状态',
                alipayLog: '支付宝状态',
                log: '请开始你的表演~'
            }
        },
        // 方法
        methods: {
            // 刷新网页
            refreshPage() {
                // 原地刷新
                location.reload()
            },
            // 设置计时器
            setTimer() {
                // 运行中状态变量设置为真
                this.running = true
                // 计时器
                this.timer = setInterval( () => {
                    this.run()
                }, this.ms);
            },
            // 关闭计时器
            closeTimer() {
                // 运行中状态变量设置为否
                this.running = false
                // 关闭计时器
                clearInterval(this.timer)
            },
            // 运行
            run() {
                // 判断需要请求的是微信还是支付宝等
                if(this.methods=='wechat'){ // 如果是微信
                    // 先改提示
                    this.wechatLog = '微信请求中... ...'
                    this.alipayLog = '支付宝无需请求！'
                    // 运行微信任务
                    this.wechatPlan()
                }else if(this.methods=='alipay'){ // 如果是支付宝
                    // 先改提示
                    this.wechatLog = '微信无需请求'
                    this.alipayLog = '支付宝请求中... ...'
                    // 运行支付宝任务
                    this.alipayPlan()
                }else if(this.methods=='all'){ // 如果是两者
                    // 先改提示
                    this.wechatLog = '微信请求中... ...'
                    this.alipayLog = '支付宝请求中... ...'
                    // 运行微信、支付宝的任务
                    this.wechatPlan()
                    this.alipayPlan()
                }
            },
            wechatPlan() {
                fetch(this.url + "/api/wechat/index")
                    .then(fetch => fetch.text())
                    .then(text => { this.wechatLogs(text) } )
            },
            alipayPlan(){
                fetch(this.url + "/api/cron/alipay")
                    .then(fetch => fetch.text())
                    .then(text => { this.alipayLogs(text) } )
            },
            wechatLogs(value) {
                if(value=='Crontab Success!'){
                    this.wechatLog = "微信监控完成！"
                }else{
                    this.wechatLog = "微信未监控或失败！"
                }
                this.log = this.wechatLog + ' | ' + this.alipayLog
            },
            alipayLogs(value) {
                if(value=='Crontab Success!'){
                    this.alipayLog = "支付宝监控完成！"
                }else{
                    this.alipayLog = "支付宝未监控或失败！"
                }
                this.log = this.wechatLog + ' | ' + this.alipayLog
            }
        },
        mounted() {
            clearInterval(this.timer)
        },
        beforeDestroy() {
            clearInterval(this.timer)
        },
    }    
</script>